import { Component, OnInit } from '@angular/core';
import { Color, Label } from 'ng2-charts';
import { ChartDataSets, ChartOptions } from 'chart.js';
import * as pluginAnnotations from 'chartjs-plugin-annotation';
import { CommonService } from '../../../../services/common.service';
import deathActualData from '../../../../../data/actual/deaths-data.json'
import { CountryIndexPair } from '../../../../constants/global-enums';



@Component({
  selector: 'app-death-actual-vs-death-prediction',
  templateUrl: './death-actual-vs-death-prediction.component.html',
  styleUrls: ['./death-actual-vs-death-prediction.component.sass']
})
export class DeathActualVsDeathPredictionComponent implements OnInit {

   // all data
   private deathActualData: any = deathActualData;
   private deathPredictionsData: any;
   
   // monthly data
   private deathActualMonthlyCases: any;
   private deathPredictionsMonthlyCases: any;
 
   // selected country
   private selectedCountryIndex;
   private activeMonthlyDataIndexes: Array<number>;
 
 
   private minYaxisDeathActualData: number;
   private maxYaxisDeathActualData: number;
   private minYaxisDeathPredictionData: number;
   private maxYaxisDeathPredictionData: number;
   private minYaxis: number;
   private maxYaxis: number;
 
 
   // chart data
   public lineChartLabels: Label[];
   public lineChartData: ChartDataSets[];
   public lineChartOptions: (ChartOptions & { annotation: any }) = {
     responsive: true,
     scales: {
       // We use this empty structure as a placeholder for dynamic theming.
       xAxes: [
         {
           scaleLabel: {
             display: true,
             labelString: 'Over Time',
             fontColor: '#F74300'
           },            
           ticks: {
             fontColor: '#979797'
           }
         }
       ],
       yAxes: [
         {
           scaleLabel: {
             display: true,
             labelString: 'Death Cases',
             fontColor: '#F74300'
           },
           gridLines: {
             color: '#dcd7d7',
           },            
           ticks: {
             fontColor: '#979797',
             max : this.minYaxis,
             min:  this.minYaxis,
             stepSize: Math.round((this.minYaxis - this.minYaxis) / (this.deathPredictionsMonthlyCases + 1))
           }
         }
       ]
     },
     elements : { 
       line : { 
         tension : 0.7,
         borderWidth: 1
       }
     },
     plugins: {
       datalabels: {
         font: {
           size: 11,
           weight: 'bold',
         },
         offset: 3,
         align: function(ctx) {
           let idx = ctx.dataIndex;
           let val = ctx.dataset.data[idx];
           let datasets = ctx.chart.data.datasets;
           let minVal = val;
           let maxVal = val;
           let i, ilen, ival;
   
           for (i = 0, ilen = datasets.length; i < ilen; ++i) {
             if (i === ctx.datasetIndex) {
               continue;
             }
   
             ival = datasets[i].data[idx];
             minVal = minVal < ival ? minVal : ival;
             maxVal = maxVal > ival ? maxVal : ival;
   
             if (val > minVal && val < maxVal) {
               return 'center';
             }
           }
   
           return val <= minVal ? 'right' : 'end';
         }
       }
     },
     annotation: {
       annotations: [
       ],
     },
     layout: {
     },
     legend: {
       display: true,
       // labels:{
       //   generateLabels: (chart) => {
       //     chart.legend.afterFit = function () {
       //       // this.height = this.height + 10;
       //     }
       //   }
       // }
     }
   };
   
   public lineChartColors: Color[] = [
     { 
       backgroundColor: 'rgba(0,0,0,0)',
       borderColor: '#DA075F',
       pointBackgroundColor: '#DA075F',
       pointBorderColor: '#fff',
       pointHoverBackgroundColor: '#fff',
       pointHoverBorderColor: '#DA075F'
     },
     { 
       backgroundColor: 'rgba(0,0,0,0)',
       borderColor: '#006300',
       pointBackgroundColor: '#006300',
       pointBorderColor: '#fff',
       pointHoverBackgroundColor: '#fff',
       pointHoverBorderColor: '#006300'
     }
   ];
   public lineChartLegend = true;
   public lineChartType = 'line';
   public lineChartPlugins = [pluginAnnotations];
 
   constructor(
     private commonService: CommonService
   ) {
       this.commonService.selectedCountry$.subscribe((countryIndex) => {
       this.deathActualMonthlyCases = [];
       this.deathPredictionsMonthlyCases = [];
       this.lineChartLabels = [];
       this.activeMonthlyDataIndexes = [];
       
       this.selectedCountryIndex = countryIndex;
       this.readPredictionData();
 
       this.lineChartData = [
         { data: this.deathActualMonthlyCases, label: 'Actual Data' },
         { data: this.deathPredictionsMonthlyCases, label: 'Prediction Data' }
       ];
      //  console.log(this.deathActualMonthlyCases, this.deathPredictionsMonthlyCases);
     });
   }
 
   ngOnInit() {
   }
 
 
   private readPredictionData() {
     const selectedCountryName = CountryIndexPair[this.selectedCountryIndex].toLowerCase();
     import(`../../../../../data/predictions/${selectedCountryName}/death-data.json`)
     .then(data => {
       // JUST USE THE LIBRARY
       this.deathPredictionsData = data.default;
       this.getDeathActualMonthlyData();
       this.getDeathPredictiveMonthlyData();  
       this.getMinMaxXValue();
     })
     .catch(error => {
       console.log(error);
     });
   }
 
 
   // get monthly Death-actual data
   private getDeathActualMonthlyData() {
     let count = 0;
     let actualTotalCount = 0;
     let PredictiveTotalCount = 0;
     for(let i = 0; i < this.deathActualData[`${this.selectedCountryIndex}`].data.length; i++) {     
       if (i === 0 || count === 30 || i === this.deathActualData[`${this.selectedCountryIndex}`].data.length - 1) {
         actualTotalCount = parseInt(this.deathActualData[`${this.selectedCountryIndex}`].data[i].count, 10);          
         this.deathActualMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
         this.activeMonthlyDataIndexes.push(i);
         count = 0;
         actualTotalCount = 0;
       }  
       count++;
     }
 
   }
 
 
   // get monthly death-prediction data
   private getDeathPredictiveMonthlyData() { 
     let actualTotalCount = 0;
     let PredictiveTotalCount = 0;
     for(let i = 0; i < Object.keys(this.deathPredictionsData).length; i++) {      
       if (this.activeMonthlyDataIndexes.indexOf(i) > -1 || i === Object.keys(this.deathPredictionsData).length - 1) {
         actualTotalCount = parseInt(this.deathPredictionsData[i]['Scored Labels'], 10)
         this.deathPredictionsMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
         this.lineChartLabels.push(this.commonService.addDays(new Date('01-22-2020'), parseInt(this.deathPredictionsData[i].Date, 10)));
         actualTotalCount = 0;
       }  
     }
     console.log(this.activeMonthlyDataIndexes, this.lineChartLabels);
   }
 
 
   private getMinMaxXValue() {
    this.minYaxisDeathActualData = this.commonService.getMinimumValue(this.deathActualMonthlyCases);
    this.maxYaxisDeathActualData = this.commonService.getMaximumValue(this.deathActualMonthlyCases);

    this.minYaxisDeathPredictionData = this.commonService.getMinimumValue(this.deathPredictionsMonthlyCases);
    this.maxYaxisDeathPredictionData = this.commonService.getMaximumValue(this.deathPredictionsMonthlyCases);

    this.minYaxis = this.minYaxisDeathActualData < this.minYaxisDeathPredictionData ? this.minYaxisDeathActualData : this.minYaxisDeathPredictionData;

    this.maxYaxis = this.maxYaxisDeathActualData > this.maxYaxisDeathPredictionData ? this.maxYaxisDeathActualData : this.maxYaxisDeathPredictionData;
    //  console.log(this.minYaxisDeathActualData, this.minYaxisDeathPredictionData, 'min = ', this.minYaxis);
    //  console.log(this.maxYaxisDeathActualData, this.maxYaxisDeathPredictionData, 'max = ', this.maxYaxis);
   }
}
