import { Injectable } from '@angular/core';
import { BehaviorSubject  } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  public selectedCountry$: BehaviorSubject <number>;

  constructor() {
    this.selectedCountry$ = new BehaviorSubject <number>(4);
  }

  // publish selected country
  public publishSelectedCountry(countryIndex: any) {
    if (countryIndex !== undefined) {
      this.selectedCountry$.next(countryIndex);
    }    
  }

  public addDays(dateObj, numDays) {
    dateObj.setDate(dateObj.getDate() + numDays);
    let dd = dateObj.getDate();
    let mm = dateObj.getMonth() + 1;
    let y = dateObj.getFullYear();
    return mm + '/'+ dd + '/'+ y;
  }


  public getMinimumValue(data: Array<number>) {
    const minVal = data.reduce((prev, current) => { 
      return (prev < current) ? prev : current; 
    });
    return Math.round(minVal);
  }

  public getMaximumValue(data: Array<number>) {
    const maxVal = data.reduce((prev, current) => { 
      return (prev > current) ? prev : current; 
    });
    return Math.round(maxVal);
  }
}
