import {Component, HostListener, OnInit} from '@angular/core';
import {ThemeOptions} from '../../../theme-options';
import {select} from '@angular-redux/store';
import {Observable} from 'rxjs';
import {ActivatedRoute} from '@angular/router';
import { CommonService } from '../../../services/common.service';
import activeData from '../../../../data/actual/active-data.json';
import confirmedData from '../../../../data/actual/confirmed-data.json';
import deathsData from '../../../../data/actual/deaths-data.json';
import recoveredData from '../../../../data/actual/recovery-data.json';
import countryDataset from '../../../../data/countries-dataset.json';


@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
})
export class SidebarComponent implements OnInit {
  public extraParameter: any;
  public country: any;
  public ASSETS_PATH = './assets';
  public globalActiveCases: number;
  public globalConfirmedCases: number;
  public globalDeathCases: number;
  public globalRecoveredCases: number;
  public globalPopulation: number;

  constructor(
    public globals: ThemeOptions, 
    private activatedRoute: ActivatedRoute,
    private commonService: CommonService
  ) { 
  }

  public countryChange() {

  }

  @select('config') public config$: Observable<any>;

  private newInnerWidth: number;
  private innerWidth: number;
  activeId = 'dashboardsMenu';

  toggleSidebar() {
    this.globals.toggleSidebar = !this.globals.toggleSidebar;
  }

  sidebarHover() {
    this.globals.sidebarHover = !this.globals.sidebarHover;
  }

  ngOnInit() {
    setTimeout(() => {
      this.innerWidth = window.innerWidth;
      if (this.innerWidth < 1200) {
        this.globals.toggleSidebar = true;
      }
    });

    this.extraParameter = this.activatedRoute.snapshot.firstChild.data.extraParameter;

    
    this.globalActiveCases = this.getGlobalActiveCases();
    this.globalConfirmedCases = this.getGlobalConfirmedCases();
    this.globalDeathCases = this.getGlobalDeathCases();
    this.globalRecoveredCases = this.getGlobalRecoveredCases();
    this.globalPopulation = countryDataset[0].population;;
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.newInnerWidth = event.target.innerWidth;

    if (this.newInnerWidth < 1200) {
      this.globals.toggleSidebar = true;
    } else {
      this.globals.toggleSidebar = false;
    }
  }

  

  // Global total active cases
  private getGlobalActiveCases(): number {
    let lastdayIndex = activeData[0].data.length - 1;
    return activeData[0].data[lastdayIndex].count;
  }

  // Global total confirmed cases
  private getGlobalConfirmedCases(): number {
    let lastdayIndex = confirmedData[0].data.length - 1;
    return confirmedData[0].data[lastdayIndex].count;
  }

  // Global total death cases
  private getGlobalDeathCases(): number {
    let lastdayIndex = deathsData[0].data.length - 1;
    return deathsData[0].data[lastdayIndex].count;
  }

  // Global total recovery cases
  private getGlobalRecoveredCases(): number {
    let lastdayIndex = recoveredData[0].data.length - 1;
    return recoveredData[0].data[lastdayIndex].count;
  }

}
