import { Component, OnInit } from '@angular/core';
import confirmedActualData from '../../../../../data/actual/confirmed-data.json';
import deathActualData from '../../../../../data/actual/deaths-data.json';
import { CommonService } from '../../../../services/common.service';
import { Label, Color } from 'ng2-charts';
import { ChartDataSets, ChartOptions } from 'chart.js';
import * as pluginAnnotations from 'chartjs-plugin-annotation';
import { DatePipe } from '@angular/common';


@Component({
  selector: 'app-confirmed-actual-vs-death-actual',
  templateUrl: './confirmed-actual-vs-death-actual.component.html',
  styleUrls: ['./confirmed-actual-vs-death-actual.component.sass']
})
export class ConfirmedActualVsDeathActualComponent implements OnInit {

  // all data
  private confirmedActualData: any = confirmedActualData;
  private deathActualData: any = deathActualData;

  // monthly data
  private confirmedActualMonthlyCases: any;
  private deathActualMonthlyCases: any;

  // selected country
  private selectedCountryIndex;


  private minYaxisConfirmedActualData: number;
  private maxYaxisConfirmedActualData: number;
  private minYaxisDeathActualData: number;
  private maxYaxisDeathActualData: number;
  private minYaxis: number;
  private maxYaxis: number;

  // chart data
  public lineChartLabels: Label[];
  public lineChartData: ChartDataSets[];
  public lineChartOptions: (ChartOptions & { annotation: any }) = {
    responsive: true,
    scales: {
      // We use this empty structure as a placeholder for dynamic theming.
      xAxes: [
        {
          scaleLabel: {
            display: true,
            labelString: 'Over Time',
            fontColor: '#F74300'
          },            
          ticks: {
            fontColor: '#979797'
          }
        }
      ],
      yAxes: [
        {
          scaleLabel: {
            display: true,
            labelString: 'Confirmed Cases',
            fontColor: '#F74300'
          },
          gridLines: {
            color: '#dcd7d7',
          },            
          ticks: {
            fontColor: '#979797',
            max : this.minYaxis,
            min:  this.minYaxis,
            stepSize: Math.round((this.minYaxis - this.minYaxis) / (this.deathActualMonthlyCases + 1))
          }
        }
      ]
    },
    elements : { 
      line : { 
        tension : 0.7,
        borderWidth: 1
      }
    },
    plugins: {
      datalabels: {
        font: {
          size: 11,
          weight: 'bold',
        },
        offset: 3,
        align: function(ctx) {
          let idx = ctx.dataIndex;
          let val = ctx.dataset.data[idx];
          let datasets = ctx.chart.data.datasets;
          let minVal = val;
          let maxVal = val;
          let i, ilen, ival;
  
          for (i = 0, ilen = datasets.length; i < ilen; ++i) {
            if (i === ctx.datasetIndex) {
              continue;
            }
  
            ival = datasets[i].data[idx];
            minVal = minVal < ival ? minVal : ival;
            maxVal = maxVal > ival ? maxVal : ival;
  
            if (val > minVal && val < maxVal) {
              return 'center';
            }
          }
  
          return val <= minVal ? 'right' : 'end';
        }
      }
    },
    annotation: {
      annotations: [
      ],
    },
    layout: {
    },
    legend: {
      display: true,
      // labels:{
      //   generateLabels: (chart) => {
      //     chart.legend.afterFit = function () {
      //       // this.height = this.height + 10;
      //     }
      //   }
      // }
    }
  };
  
  public lineChartColors: Color[] = [
    { 
      backgroundColor: 'rgba(0,0,0,0)',
      borderColor: '#00BFFE',
      pointBackgroundColor: '#00BFFE',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: '#00BFFE'
    },
    { 
      backgroundColor: 'rgba(0,0,0,0)',
      borderColor: '#FE0002',
      pointBackgroundColor: '#FE0002',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: '#FE0002'
    }
  ];
  public lineChartLegend = true;
  public lineChartType = 'line';
  public lineChartPlugins = [pluginAnnotations];

  



  constructor(
    private commonService: CommonService,
    private datepipe: DatePipe
  ) {
    this.commonService.selectedCountry$.subscribe((countryIndex) => {
      this.confirmedActualMonthlyCases = [];
      this.deathActualMonthlyCases = [];
      this.lineChartLabels = [];
      
      this.selectedCountryIndex = countryIndex;

      this.getConfirmedActualMonthlyData();
      this.getDeathActualMonthlyData();
      this.getMinMaxXValue();

      // console.log(this.confirmedActualMonthlyCases, this.deathActualMonthlyCases);

      this.lineChartData = [
        { data: this.confirmedActualMonthlyCases, label: 'Confirmed Cases' },
        { data: this.deathActualMonthlyCases, label: 'Death Cases' }
      ];
    });
  }

  ngOnInit() {
  }


    // get monthly confirmed-actual data
    private getConfirmedActualMonthlyData() {
      let count = 0;
      let actualTotalCount = 0;
      let PredictiveTotalCount = 0;
      for(let i = 0; i < this.confirmedActualData[`${this.selectedCountryIndex}`].data.length; i++) {     

        if (i === 0 || count === 30 || i === this.confirmedActualData[`${this.selectedCountryIndex}`].data.length - 1) {
          actualTotalCount = parseInt(this.confirmedActualData[`${this.selectedCountryIndex}`].data[i].count, 10);          
          this.confirmedActualMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
          count = 0;
          actualTotalCount = 0;
        }  
        count++;
      }
      // console.log(this.confirmedActualMonthlyCases);
    }
  
  
    // get monthly death-actual data
    private getDeathActualMonthlyData() {    
      let count = 0;
      let actualTotalCount = 0;
      let PredictiveTotalCount = 0;
      let labels;
      for(let i = 0; i < this.deathActualData[`${this.selectedCountryIndex}`].data.length; i++) {      
        if (i === 0 || count === 30 || i === this.deathActualData[`${this.selectedCountryIndex}`].data.length - 1) {
          actualTotalCount = parseInt(this.deathActualData[`${this.selectedCountryIndex}`].data[i].count, 10);
          this.deathActualMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
          labels = this.datepipe.transform(new Date(this.deathActualData[`${this.selectedCountryIndex}`].data[i].date), 'M/dd/yyyy')
          this.lineChartLabels.push(labels);
          count = 0;
          actualTotalCount = 0;
        }
        count++;
      }
    }

    private getMinMaxXValue() {
      this.minYaxisConfirmedActualData = this.commonService.getMinimumValue(this.confirmedActualMonthlyCases);
      this.maxYaxisConfirmedActualData = this.commonService.getMaximumValue(this.confirmedActualMonthlyCases);

      this.minYaxisDeathActualData = this.commonService.getMinimumValue(this.deathActualMonthlyCases);
      this.maxYaxisDeathActualData = this.commonService.getMaximumValue(this.deathActualMonthlyCases);
      this.minYaxis = this.minYaxisConfirmedActualData < this.minYaxisDeathActualData ? this.minYaxisConfirmedActualData : this.minYaxisDeathActualData;
      this.maxYaxis = this.maxYaxisConfirmedActualData > this.maxYaxisDeathActualData ? this.maxYaxisConfirmedActualData : this.maxYaxisDeathActualData;
      // console.log(this.minYaxisConfirmedActualData, this.minYaxisDeathActualData, 'min = ', this.minYaxis);
      // console.log(this.maxYaxisConfirmedActualData, this.maxYaxisDeathActualData, 'max = ', this.maxYaxis);
    }
  

}
