import { Component, OnInit } from '@angular/core';
import {Color} from 'ng2-charts/ng2-charts';
import { CommonService } from '../../../services/common.service';
import { CountryIndexPair } from '../../../constants/global-enums';
import activeData from '../../../../data/actual/active-data.json';
import confirmedData from '../../../../data/actual/confirmed-data.json';
import deathsData from '../../../../data/actual/deaths-data.json';
import recoveredData from '../../../../data/actual/recovery-data.json';
import countryDataset from '../../../../data/countries-dataset.json';

@Component({
  selector: 'app-analytics',
  templateUrl: './analytics.component.html',
})
export class AnalyticsComponent implements OnInit {
  public selectedCountry: any;
  public countries: any;
  public country: any;
  public ASSETS_PATH = './assets';
  public confirmedData = confirmedData;

  public totalActiveCases: number;
  public totalConfirmedCases: number;
  public totalDeathCases: number;
  public totalRecoveredCases: number;

  public totalPopulation: number;

  constructor(
    private commonService: CommonService
  ) {
    this.countries = [];
    this.getCountries();
    this.country = this.countries[4];
    this.selectedCountry = this.country;
    this.totalPopulation = countryDataset[4].population;
  }

  ngOnInit() { 
    this.commonService.publishSelectedCountry(CountryIndexPair[this.selectedCountry.name]);  
    this.totalActiveCases = this.getTotalActiveCases();
    this.totalConfirmedCases = this.getTotalConfirmedCases();
    this.totalDeathCases = this.getTotalDeathCases();
    this.totalRecoveredCases = this.getTotalRecoveredCases();
  }

  private getCountries() {
    // countryDataset.sort((a,b) => a.country_name < b.country_name ? -1 : 1);;
    for(let i =0; i<countryDataset.length; i++) {
      this.countries.push({name: countryDataset[i].country_name, index: i});      
    }
    
  }

  public countryChange(event) {
    this.selectedCountry = event;
    this.totalPopulation = countryDataset[event.index].population;
    this.commonService.publishSelectedCountry(CountryIndexPair[this.selectedCountry.name]);    
    this.totalActiveCases = this.getTotalActiveCases();
    this.totalConfirmedCases = this.getTotalConfirmedCases();
    this.totalDeathCases = this.getTotalDeathCases();
    this.totalRecoveredCases = this.getTotalRecoveredCases();
  }

  private getTotalActiveCases(): number {
    let lastdayIndex = activeData[`${CountryIndexPair[this.selectedCountry.name]}`].data.length - 1;
    return activeData[`${CountryIndexPair[this.selectedCountry.name]}`].data[lastdayIndex].count;
  }

  private getTotalConfirmedCases(): number {
    let lastdayIndex = confirmedData[`${CountryIndexPair[this.selectedCountry.name]}`].data.length - 1;
    return confirmedData[`${CountryIndexPair[this.selectedCountry.name]}`].data[lastdayIndex].count;
  }

  private getTotalDeathCases(): number {
    let lastdayIndex = deathsData[`${CountryIndexPair[this.selectedCountry.name]}`].data.length - 1;
    return deathsData[`${CountryIndexPair[this.selectedCountry.name]}`].data[lastdayIndex].count;
  }

  private getTotalRecoveredCases(): number {
    let lastdayIndex = recoveredData[`${CountryIndexPair[this.selectedCountry.name]}`].data.length - 1;
    return recoveredData[`${CountryIndexPair[this.selectedCountry.name]}`].data[lastdayIndex].count;
  }
}
