  private readPredictionData() {
    const selectedCountryName = CountryIndexPair[this.selectedCountryIndex].toLowerCase();
    import(`../../../../../data/predictions/${selectedCountryName}/confirmed-data.json`)
    .then(data => {
      // JUST USE THE LIBRARY
      this.confirmedPredictionsData = data.default;
      this.getConfirmedActualMonthlyData();
      this.getConfirmedPredictiveMonthlyData();  
      this.getMinMaxXValue();
    })
    .catch(error => {
      console.log(error);
    });
  }


  // get monthly confirmed-actual data
  private getConfirmedActualMonthlyData() {
    let count = 0;
    let actualTotalCount = 0;
    let PredictiveTotalCount = 0;
    for(let i = 0; i < this.confirmedActualData[`${this.selectedCountryIndex}`].data.length; i++) {     
      if (i === 0 || count === 30 || i === this.confirmedActualData[`${this.selectedCountryIndex}`].data.length - 1) {
        actualTotalCount = parseInt(this.confirmedActualData[`${this.selectedCountryIndex}`].data[i].count, 10);
        this.confirmedActualMonthlyData.push({'date': this.confirmedActualData[`${this.selectedCountryIndex}`].data[i].date, actualTotalCount});
        this.confirmedActualMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
        this.activeMonthlyDataIndexes.push(i);
        count = 0;
        actualTotalCount = 0;
      }  
      count++;
    }
  }


  // get monthly confirmed-predictive data
  private getConfirmedPredictiveMonthlyData() {   
    let actualTotalCount = 0;
    let PredictiveTotalCount = 0;
    for(let i = 0; i < Object.keys(this.confirmedPredictionsData).length; i++) {      
      if (this.activeMonthlyDataIndexes.indexOf(i) > -1 || i === Object.keys(this.confirmedPredictionsData).length - 1) {
        actualTotalCount = parseInt(this.confirmedPredictionsData[i]['Scored Labels'], 10);

        this.confirmedPredictionsMonthlyData.push({'date': this.commonService.addDays(new Date('01-22-2020'), parseInt(this.confirmedPredictionsData[i].Date, 10)), actualTotalCount});
        
        this.confirmedPredictionsMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
        this.lineChartLabels.push(this.commonService.addDays(new Date('01-22-2020'), parseInt(this.confirmedPredictionsData[i].Date, 10)));       
        actualTotalCount = 0;
      }
    }
    console.log(this.activeMonthlyDataIndexes, this.lineChartLabels);
  }


  private getMinMaxXValue() {
    this.minYaxisConfirmedActualData = this.getMinimumValue(this.confirmedActualMonthlyData, 'actualTotalCount');
    this.maxYaxisConfirmedActualData = this.getMaximumValue(this.confirmedActualMonthlyData, 'actualTotalCount');

    this.minYaxisConfirmedPredictionsData = this.getMinimumValue(this.confirmedPredictionsMonthlyData, 'actualTotalCount');
    this.maxYaxisConfirmedPredictionsData = this.getMaximumValue(this.confirmedPredictionsMonthlyData, 'actualTotalCount');
    this.minYaxis = this.minYaxisConfirmedActualData < this.minYaxisConfirmedPredictionsData ? this.minYaxisConfirmedActualData : this.minYaxisConfirmedPredictionsData;
    this.maxYaxis = this.maxYaxisConfirmedActualData > this.maxYaxisConfirmedPredictionsData ? this.maxYaxisConfirmedActualData : this.maxYaxisConfirmedPredictionsData;
    // console.log(this.minYaxisConfirmedActualData, this.minYaxisConfirmedPredictionsData, 'min = ', this.minYaxis);
    // console.log(this.maxYaxisConfirmedActualData, this.maxYaxisConfirmedPredictionsData, 'max = ', this.maxYaxis);
  }


  private getMinimumValue(data: Array<object>, keyValue: string) {
    const minVal = data.reduce((prev, current) => { 
        return (prev[`${keyValue}`] < current[`${keyValue}`]) ? prev : current; 
    });
    return Math.round(minVal[`${keyValue}`]);
  }

  private getMaximumValue(data: Array<object>, keyValue: string) {
    const maxVal = data.reduce((prev, current) => { 
      return (prev[`${keyValue}`] > current[`${keyValue}`]) ? prev : current; 
    });
    return Math.round(maxVal[`${keyValue}`]);
  }


  

