import { Component, OnInit } from '@angular/core';
import { Color, Label } from 'ng2-charts';
import { ChartDataSets, ChartOptions } from 'chart.js';
import * as pluginAnnotations from 'chartjs-plugin-annotation';
import { CommonService } from '../../../../services/common.service';
import recoveryActualData from '../../../../../data/actual/recovery-data.json'
import { CountryIndexPair } from '../../../../constants/global-enums';


@Component({
  selector: 'app-recovery-actual-vs-recovery-prediction',
  templateUrl: './recovery-actual-vs-recovery-prediction.component.html',
  styleUrls: ['./recovery-actual-vs-recovery-prediction.component.sass']
})
export class RecoveryActualVsRecoveryPredictionComponent implements OnInit {

  // all data
  private recoveryActualData: any = recoveryActualData;
  private recoveryPredictionsData: any;
  
  // monthly data
  private recoveryActualMonthlyCases: any;
  private recoveryPredictionsMonthlyCases: any;

  // selected country
  private selectedCountryIndex;

  private activeMonthlyDataIndexes: Array<number>;


  private minYaxisRecoveryActualData: number;
  private maxYaxisRecoveryActualData: number;
  private minYaxisRecoveryPredictionData: number;
  private maxYaxisRecoveryPredictionData: number;
  private minYaxis: number;
  private maxYaxis: number;


  // chart data
  public lineChartLabels: Label[];
  public lineChartData: ChartDataSets[];
  public lineChartOptions: (ChartOptions & { annotation: any }) = {
    responsive: true,
    scales: {
      // We use this empty structure as a placeholder for dynamic theming.
      xAxes: [
        {
          scaleLabel: {
            display: true,
            labelString: 'Over Time',
            fontColor: '#F74300'
          },            
          ticks: {
            fontColor: '#979797'
          }
        }
      ],
      yAxes: [
        {
          scaleLabel: {
            display: true,
            labelString: 'Recovery Cases',
            fontColor: '#F74300'
          },
          gridLines: {
            color: '#dcd7d7',
          },            
          ticks: {
            fontColor: '#979797',
            max : this.minYaxis,
            min:  this.minYaxis,
            stepSize: Math.round((this.minYaxis - this.minYaxis) / (this.recoveryPredictionsMonthlyCases + 1))
          }
        }
      ]
    },
    elements : { 
      line : { 
        tension : 0.7,
        borderWidth: 1
      }
    },
    plugins: {
      datalabels: {
        font: {
          size: 11,
          weight: 'bold',
        },
        offset: 3,
        align: function(ctx) {
          let idx = ctx.dataIndex;
          let val = ctx.dataset.data[idx];
          let datasets = ctx.chart.data.datasets;
          let minVal = val;
          let maxVal = val;
          let i, ilen, ival;
  
          for (i = 0, ilen = datasets.length; i < ilen; ++i) {
            if (i === ctx.datasetIndex) {
              continue;
            }
  
            ival = datasets[i].data[idx];
            minVal = minVal < ival ? minVal : ival;
            maxVal = maxVal > ival ? maxVal : ival;
  
            if (val > minVal && val < maxVal) {
              return 'center';
            }
          }
  
          return val <= minVal ? 'right' : 'end';
        }
      }
    },
    annotation: {
      annotations: [
      ],
    },
    layout: {
    },
    legend: {
      display: true,
      // labels:{
      //   generateLabels: (chart) => {
      //     chart.legend.afterFit = function () {
      //       // this.height = this.height + 10;
      //     }
      //   }
      // }
    }
  };
  
  public lineChartColors: Color[] = [
    { 
      backgroundColor: 'rgba(0,0,0,0)',
      borderColor: '#DA075F',
      pointBackgroundColor: '#DA075F',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: '#DA075F'
    },
    { 
      backgroundColor: 'rgba(0,0,0,0)',
      borderColor: '#006300',
      pointBackgroundColor: '#006300',
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: '#006300'
    }
  ];
  public lineChartLegend = true;
  public lineChartType = 'line';
  public lineChartPlugins = [pluginAnnotations];

  constructor(
    private commonService: CommonService
  ) {
      this.commonService.selectedCountry$.subscribe((countryIndex) => {
      this.recoveryActualMonthlyCases = [];
      this.recoveryPredictionsMonthlyCases = [];
      this.lineChartLabels = [];
      this.activeMonthlyDataIndexes = [];
      
      this.selectedCountryIndex = countryIndex;
      this.readPredictionData();

      this.lineChartData = [
        { data: this.recoveryActualMonthlyCases, label: 'Actual Data' },
        { data: this.recoveryPredictionsMonthlyCases, label: 'Prediction Data' }
      ];
      // console.log(this.recoveryActualMonthlyCases, this.recoveryPredictionsMonthlyCases);
    });
  }

  ngOnInit() {
  }


  private readPredictionData() {
    const selectedCountryName = CountryIndexPair[this.selectedCountryIndex].toLowerCase();
    import(`../../../../../data/predictions/${selectedCountryName}/recovery-data.json`)
    .then(data => {
      // JUST USE THE LIBRARY
      this.recoveryPredictionsData = data.default;
      this.getRecoveryActualMonthlyData();
      this.getRecoveryPredictiveMonthlyData();  
      this.getMinMaxXValue();
    })
    .catch(error => {
      console.log(error);
    });
  }


  // get monthly recovery-actual data
  private getRecoveryActualMonthlyData() {
    let count = 0;
    let actualTotalCount = 0;
    let PredictiveTotalCount = 0;
    for(let i = 0; i < this.recoveryActualData[`${this.selectedCountryIndex}`].data.length; i++) {     
      if (i === 0 || count === 30 || i === this.recoveryActualData[`${this.selectedCountryIndex}`].data.length - 1) {
        actualTotalCount = parseInt(this.recoveryActualData[`${this.selectedCountryIndex}`].data[i].count, 10);          
        this.recoveryActualMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
        this.activeMonthlyDataIndexes.push(i);
        count = 0;
        actualTotalCount = 0;
      }  
      count++;
    }

  }


  // get monthly recovery-prediction data
  private getRecoveryPredictiveMonthlyData() {   
    let actualTotalCount = 0;
    let PredictiveTotalCount = 0;
    for(let i = 0; i < Object.keys(this.recoveryPredictionsData).length; i++) {      
      if (this.activeMonthlyDataIndexes.indexOf(i) > -1 || i === Object.keys(this.recoveryPredictionsData).length - 1) {
        actualTotalCount = parseInt(this.recoveryPredictionsData[i]['Scored Labels'], 10)
        this.recoveryPredictionsMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
        this.lineChartLabels.push(this.commonService.addDays(new Date('01-22-2020'), parseInt(this.recoveryPredictionsData[i].Date, 10)));
        actualTotalCount = 0;
      }  
    }
    console.log(this.activeMonthlyDataIndexes, this.lineChartLabels);
  }


  private getMinMaxXValue() {
    this.minYaxisRecoveryActualData = this.commonService.getMinimumValue(this.recoveryActualMonthlyCases);
    this.maxYaxisRecoveryActualData = this.commonService.getMaximumValue(this.recoveryActualMonthlyCases);

    this.minYaxisRecoveryPredictionData = this.commonService.getMinimumValue(this.recoveryPredictionsMonthlyCases);
    this.maxYaxisRecoveryPredictionData = this.commonService.getMaximumValue(this.recoveryPredictionsMonthlyCases);

    this.minYaxis = this.minYaxisRecoveryActualData < this.minYaxisRecoveryPredictionData ? this.minYaxisRecoveryActualData : this.minYaxisRecoveryPredictionData;

    this.maxYaxis = this.maxYaxisRecoveryActualData > this.maxYaxisRecoveryPredictionData ? this.maxYaxisRecoveryActualData : this.maxYaxisRecoveryPredictionData;
    // console.log(this.minYaxisRecoveryActualData, this.minYaxisRecoveryPredictionData, 'min = ', this.minYaxis);
    // console.log(this.maxYaxisRecoveryActualData, this.maxYaxisRecoveryPredictionData, 'max = ', this.maxYaxis);
  }

}
