import { Component, OnInit } from '@angular/core';
import { Color, Label } from 'ng2-charts';
import { ChartDataSets, ChartOptions } from 'chart.js';
import * as pluginAnnotations from 'chartjs-plugin-annotation';
import { CommonService } from '../../../../services/common.service';
import activeActualData from '../../../../../data/actual/active-data.json'
import { CountryIndexPair } from '../../../../constants/global-enums';



@Component({
  selector: 'app-active-actual-vs-active-prediction',
  templateUrl: './active-actual-vs-active-prediction.component.html',
  styleUrls: ['./active-actual-vs-active-prediction.component.sass']
})
export class ActiveActualVsActivePredictionComponent implements OnInit {
   // all data
   private activeActualData: any = activeActualData;
   private activePredictionsData: any;
   
   // monthly data
   private activeActualMonthlyCases: any;
   private activePredictionsMonthlyCases: any;
 
   // selected country
   private selectedCountryIndex;

   private activeMonthlyDataIndexes: Array<number>;
 
 
   private minYaxisActiveActualData: number;
   private maxYaxisActiveActualData: number;
   private minYaxisActivePredictionData: number;
   private maxYaxisActivePredictionData: number;
   private minYaxis: number;
   private maxYaxis: number;
 
 
   // chart data
   public lineChartLabels: Label[];
   public lineChartData: ChartDataSets[];
   public lineChartOptions: (ChartOptions & { annotation: any }) = {
     responsive: true,
     scales: {
       // We use this empty structure as a placeholder for dynamic theming.
       xAxes: [
         {
           scaleLabel: {
             display: true,
             labelString: 'Over Time',
             fontColor: '#F74300'
           },            
           ticks: {
             fontColor: '#979797'
           }
         }
       ],
       yAxes: [
         {
           scaleLabel: {
             display: true,
             labelString: 'Active Cases',
             fontColor: '#F74300'
           },
           gridLines: {
             color: '#dcd7d7',
           },            
           ticks: {
             fontColor: '#979797',
             max : this.minYaxis,
             min:  this.minYaxis,
             stepSize: Math.round((this.minYaxis - this.minYaxis) / (this.activePredictionsMonthlyCases + 1))
           }
         }
       ]
     },
     elements : { 
       line : { 
         tension : 0.7,
         borderWidth: 1
       }
     },
     plugins: {
       datalabels: {
         font: {
           size: 11,
           weight: 'bold',
         },
         offset: 3,
         align: function(ctx) {
           let idx = ctx.dataIndex;
           let val = ctx.dataset.data[idx];
           let datasets = ctx.chart.data.datasets;
           let minVal = val;
           let maxVal = val;
           let i, ilen, ival;
   
           for (i = 0, ilen = datasets.length; i < ilen; ++i) {
             if (i === ctx.datasetIndex) {
               continue;
             }
   
             ival = datasets[i].data[idx];
             minVal = minVal < ival ? minVal : ival;
             maxVal = maxVal > ival ? maxVal : ival;
   
             if (val > minVal && val < maxVal) {
               return 'center';
             }
           }
   
           return val <= minVal ? 'right' : 'end';
         }
       }
     },
     annotation: {
       annotations: [
       ],
     },
     layout: {
     },
     legend: {
       display: true,
       // labels:{
       //   generateLabels: (chart) => {
       //     chart.legend.afterFit = function () {
       //       // this.height = this.height + 10;
       //     }
       //   }
       // }
     }
   };
   
   public lineChartColors: Color[] = [
     { 
       backgroundColor: 'rgba(0,0,0,0)',
       borderColor: '#DA075F',
       pointBackgroundColor: '#DA075F',
       pointBorderColor: '#fff',
       pointHoverBackgroundColor: '#fff',
       pointHoverBorderColor: '#DA075F'
     },
     { 
       backgroundColor: 'rgba(0,0,0,0)',
       borderColor: '#006300',
       pointBackgroundColor: '#006300',
       pointBorderColor: '#fff',
       pointHoverBackgroundColor: '#fff',
       pointHoverBorderColor: '#006300'
     }
   ];
   public lineChartLegend = true;
   public lineChartType = 'line';
   public lineChartPlugins = [pluginAnnotations];
 
   constructor(
     private commonService: CommonService
   ) {
      this.commonService.selectedCountry$.subscribe((countryIndex) => {
      this.activeActualMonthlyCases = [];
      this.activePredictionsMonthlyCases = [];
      this.lineChartLabels = [];
      this.activeMonthlyDataIndexes = [];
      
      this.selectedCountryIndex = countryIndex;
      this.readPredictionData();

      this.lineChartData = [
        { data: this.activeActualMonthlyCases, label: 'Actual Data' },
        { data: this.activePredictionsMonthlyCases, label: 'Prediction Data' }
      ];
      //  console.log(this.activeActualMonthlyCases, this.activePredictionsMonthlyCases);
     });
   }
 
   ngOnInit() {
   }
 
 
   private readPredictionData() {
     const selectedCountryName = CountryIndexPair[this.selectedCountryIndex].toLowerCase();
     import(`../../../../../data/predictions/${selectedCountryName}/active-data.json`)
      .then(data => {
       // JUST USE THE LIBRARY
       this.activePredictionsData = data.default;
       this.getActiveActualMonthlyData();
       this.getActivePredictiveMonthlyData();  
       this.getMinMaxXValue();
      })
      .catch(error => {
       console.log(error);
      });
   }
 
 
   // get monthly active-actual data
   private getActiveActualMonthlyData() {
     let count = 0;
     let actualTotalCount = 0;
     let PredictiveTotalCount = 0;
     for(let i = 0; i < this.activeActualData[`${this.selectedCountryIndex}`].data.length; i++) {     
        if (i === 0 || count === 30 || i === this.activeActualData[`${this.selectedCountryIndex}`].data.length - 1) {
         actualTotalCount = parseInt(this.activeActualData[`${this.selectedCountryIndex}`].data[i].count, 10);          
         this.activeActualMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
         this.activeMonthlyDataIndexes.push(i);
         count = 0;
         actualTotalCount = 0;
         
        }
       count++;
     }
 
   }
 
 
   // get monthly active-prediction data
   private getActivePredictiveMonthlyData() {
     let actualTotalCount = 0;
     let PredictiveTotalCount = 0;
     for(let i = 0; i < Object.keys(this.activePredictionsData).length; i++) {      
        if (this.activeMonthlyDataIndexes.indexOf(i) > -1 || i === this.activePredictionsData.length - 1) {
         actualTotalCount = parseInt(this.activePredictionsData[i]['Scored Labels'], 10)
         this.activePredictionsMonthlyCases.push(actualTotalCount >= 0 ? actualTotalCount : 0);
         this.lineChartLabels.push(this.commonService.addDays(new Date('01-22-2020'), parseInt(this.activePredictionsData[i]['Date'], 10)));
         actualTotalCount = 0;
      }
     }
     console.log(this.activeMonthlyDataIndexes, this.lineChartLabels);
   }
 
 
   private getMinMaxXValue() {
     this.minYaxisActiveActualData = this.commonService.getMinimumValue(this.activeActualMonthlyCases);
     this.maxYaxisActiveActualData = this.commonService.getMaximumValue(this.activeActualMonthlyCases);
 
     this.minYaxisActivePredictionData = this.commonService.getMinimumValue(this.activePredictionsMonthlyCases);
     this.maxYaxisActivePredictionData = this.commonService.getMaximumValue(this.activePredictionsMonthlyCases);
 
     this.minYaxis = this.minYaxisActiveActualData < this.minYaxisActivePredictionData ? this.minYaxisActiveActualData : this.minYaxisActivePredictionData;
 
     this.maxYaxis = this.maxYaxisActiveActualData > this.maxYaxisActivePredictionData ? this.maxYaxisActiveActualData : this.maxYaxisActivePredictionData;
    //  console.log(this.minYaxisActiveActualData, this.minYaxisActivePredictionData, 'min = ', this.minYaxis);
    //  console.log(this.maxYaxisActiveActualData, this.maxYaxisActivePredictionData, 'max = ', this.maxYaxis);
   }



}
